import MarkdownIt from 'markdown-it';
import hljs from 'highlight.js/lib/highlight';
import { getUrl } from '../api';

const isProd = process.env.NODE_ENV === 'production';

export const SPLITTER = '\\\\';

// eslint-disable-next-line import/prefer-default-export
export const markdown = (text) => {
  const md = new MarkdownIt({
    highlight(str) {
      return hljs.highlight('json', str).value;
    },
  });
  if (!isProd) {
    const originRender = md.renderer.rules.image;
    md.renderer.rules.image = function render(tokens, idx, options, env, self) {
      const token = tokens[idx];
      const srcIndex = token.attrIndex('src');
      token.attrs[srcIndex][1] = getUrl() + token.attrGet('src');

      return originRender(tokens, idx, options, env, self);
    };
  }
  return md.render(text);
};

const typeMaps = {
  string: ['date', 'datetime',
    'word', 'cword', 'cw',
    'title', 'ctitle', 'ct',
    'sentence', 'csentence', 'cs',
    'summary', 'csummary', 'cm',
    'paragraph', 'cparagraph', 'cp',
    'name', 'cname',
    'uuid', 'url', 'domain', 'ip', 'email', 'image'],
  number: ['int', 'posint', 'negint', 'float', 'posfloat', 'negfloat', 'timestamp'],
};

export const getParentType = (type) => {
  const finder = ['string', 'number'].filter(parent => typeMaps[parent].includes(type.toLowerCase()));
  return finder.length > 0 ? finder[0] : '';
};
